import React from 'react';
import './Business.css';

const starRatings = {
'oneStar': 'one-star-rating',
'twoStar': 'two-star-rating',
'threeStar': 'three-star-rating',
'fourStar': 'four-star-rating',
'fiveStar': 'five-star-rating'
};


class Business extends React.Component {
  constructor(props) {
    super(props);

    this.handleStarRating = this.handleStarRating.bind(this);
  }

  handleStarRating(rating) {
    let starImage = <div className="rating-star" style={{height: 10}} />;
    console.log(starImage);
    return <div className="rating-star" style={{height: 10}} />;
  }

  getStarClass(rating) {
    let cleanRating;
    switch (rating) {
    case 5:
    cleanRating = "five";
    break;
    case 4.5:
    cleanRating = "four-half";
    break;
    case 4:
    cleanRating = "four";
    break;
    case 3.5:
    cleanRating = "three-half";
    break;
    case 3:
    cleanRating = "three";
    break;
    case 2.5:
    cleanRating = "two-half";
    break;
    case 2:
    cleanRating = "two";
    break;
    case 1.5:
    cleanRating = "one-half";
    break;
    case 1:
    cleanRating = "one";
    break;
  }
    return `star-rating-${cleanRating}`;
  }

  getStarRating(rating) {
    if (rating) {
      return <div className={this.getStarClass(rating)}></div>
    } else {
      return ''
    }
  };

  render() {
    return (
      <div className="Business">
        <div className="image-container">
          <img src={this.props.business.imageSrc} alt=''/>
        </div>
        <div className="business-card">
        <h2 className="business-name">{this.props.business.name}</h2>
        <div className="Business-reviews">
          <p className="rating">{this.getStarRating(this.props.business.rating)}</p>
          <p className="review"> {this.props.business.reviewCount} reviews</p>
        </div>
        <div className="Business-information">
          <div className="Business-address">
            <p>{this.props.business.address} {this.props.business.city}, {this.props.business.state} {this.props.business.zipCode}</p>
          </div>
        </div>
        </div>
      </div>
    );
  }
}

export default Business;
